
/**
 *    
 * */
export class Validators {

  /**
   * :   
   * @return {string}
   */
  static required(value = '') {
    return value && value.trim()
  }

  /**
   * :   
   * @return {function}
   */
  static minLength(length) {
    return value => {
      return value.length >= length
    }
  }
}
