
/**
 *       
 * */
export default class Form {

  /**
   * 
   * @param {Element} form      - .
   * @param {Object=} controls  -  .
   */
  constructor(form, controls= {}) {
    this.form = form

    this.controls = controls
  }

  /**
   *     
   * @return {Object}
   */
  value() {
    const value = {}

    Object.keys(this.controls).forEach(control => {
      value[control] = this.form[control].value
    })

    return value
  }

  /**
   *   
   * @return {boolean}
   */
  isValid() {

    let isFormValid = true // 

    Object.keys(this.controls).forEach(control => {

      if(this.controls[control].length) {

        const validators = this.controls[control] //   

        let isValid = true // 

        validators.forEach(validator =>{

          isValid = validator(this.form[control].value) && isValid //    

        })

        //   
        isValid ? clearError(this.form[control]) : setError(this.form[control])
        //   

        isFormValid = isFormValid && isValid //  

      }

    })

    return isFormValid
  }

  /**
   *  
   * @return {void}
   */
  clear() {
    this.form.reset()
  }

}

/**
 *    
 * @return {void}
 */
function setError($control){

  clearError($control) //    

  const error = '<span class="form__validation-error"> </span>' //    

  $control.nextElementSibling.firstElementChild.style.backgroundColor = '#fff5f5' //      

  $control.previousElementSibling.insertAdjacentHTML('beforeend', error) //       
}

/**
 *    
 * @return {void}
 */
function clearError($control){

  $control.nextElementSibling.firstElementChild.style.backgroundColor = '' //  

  //    
  if($control.previousElementSibling.querySelector('.form__validation-error')){

    $control.previousElementSibling.querySelector('.form__validation-error').remove() //  

  }

}
