
/**
 *    
 *  @param {Object} card - 
 *  @param {number} [card.idCard] - id
 *  @param {string} [card.programName] -  
 *  @param {string} [card.cardNumber] -  
 *  @param {string} [card.attestationDate] -  
 *  @param {string} [card.nextAttestationDate] -   
 *  @param {Object} options - 
 *  @param {?number} [options.idUser] - id 
 *  @param {?number} [options.customUser] -    
 *  @return {string}
 * */
export function cardTemplate({idCard,programName,cardNumber,attestationDate,nextAttestationDate},
 {
   idUser = null,
   customUser = null
 })
{

  return `
    <div class="result__row result__row--inner">
      <div class="row gx-0">
        <div class="col-5 g-justify-items-left" title="${programName || ' '}">
          <span class="result__clip">
            ${programName || ' '}
          </span>
        </div>
        <div class="col-3" title=" : ${cardNumber || ' '}&#10 : ${attestationDate || ' '}">
          <span class="result__clip text-align-center">
            ${cardNumber || ' '}
          </span>
        </div>
        <div class="col-2">${nextAttestationDate || ' '}</div>
        <div class="col-2">
          <span class="result__options-card">
            <button class="button button--text js-edit-card-modal js-edit-card"" type="button" data-sumbiot-target="#edit-card-modal" data-id="${idCard}" data-id-user="${idUser}" data-custom-user="${customUser}" data-action="/editCard"></button>
            <span class="p-relative d-inline-block">
              <button class="button button--text button--img-delete js-delete-user-and-card-modal" type="button" data-sumbiot-target="#delete-user-or-card-modal" data-id="${idCard}" data-id-user="${idUser}" data-custom-user="${customUser}" data-action="/deleteCard" title=" "></button>
            </span>
          </span>
        </div>
      </div>
    </div>
  `
}
