import {cardTemplate} from "../card/ard.template";
import {trainingTemplate} from "../card/training.template";
import {cardRecertificationTemplate} from "../card/cardRecertification.template";

import {cardPlugTemplate} from "../card/cardPlug.template";

/**
 *      
 *  @param {Object} user - 
 *  @param {number} [user.idUser] - id
 *  @param {Object} [user.cards] -    ['NORMAL_DATE']   ['OVER_DATE'] 
 *  @param {Array} [user.training] - 
 *  @param {number} [user.customUser] - 
 *  @param {Object} options - 
 *  @param {number} [options.build] -      1-, 2- , 0-   Hse
 *  @return {string}
 * */
export function userCardInfoTemplate({idUser,cards,training,customUser},
                                     {build = 0} = {}
) {

  /**
   *   
   * @return {string}
   */
  const renderCard = () => {
    if(cards && cards['NORMAL_DATE']){
      return cards['NORMAL_DATE'].map(card => cardTemplate(card,{idUser,customUser})).join(' ')
    }else {
      return cardPlugTemplate(' ')
    }
  }

  /**
   *  
   * @return {string}
   */
  const renderTraining = () => {
    if(training && training.length){
      return training.map(training => trainingTemplate(training,{idUser,customUser})).join(' ')
    }else {
      return cardPlugTemplate(' ')
    }
  }

  /**
   *    
   * @return {string}
   */
  const renderRecertification = () => {
    if(cards && cards['OVER_DATE']){
      return cards['OVER_DATE'].map(card => cardRecertificationTemplate(card,{idUser,customUser})).join(' ')
    }else {
      return cardPlugTemplate(' ')
    }
  }

  /**
   *  
   * @return {string}
   */
  const renderUserConfig = () => {

    //  
    if(build === 1){
      return  `
        <button class="result__info-options-btn button button--icon js-edit-user-modal" type="button" data-sumbiot-target="#edit-user-modal" data-id="${idUser}" title=" ">
          <img class="result__img" src="${BX.message('TemplateFolder')}/assets/img/edit-user-icon.svg" width="22" height="22" alt="">
        </button>
        <span class="p-relative d-inline-block">
          <button class="result__info-options-btn button button--icon js-delete-user-and-card-modal" type="button" data-sumbiot-target="#delete-user-or-card-modal" data-id="${idUser}" data-action="/deleteUser" title=" ">
            <img class="result__img" src="${BX.message('TemplateFolder')}/assets/img/remove-user-icon.svg" width="22" height="22" alt="">
          </button>
        </span>
      `
    }
    //    BX
    else if (build === 2) {
      return  `
        <button class="result__info-options-btn button button--icon js-edit-hse-modal" type="button" data-sumbiot-target="#edit-hse-modal" data-id="${idUser}" title="  HSE">
          <img class="result__img" src="${BX.message('TemplateFolder')}/assets/img/edit-document-icon.svg" width="22" height="22" alt="">
        </button>
      `
    }
    //    BX  HSE
    else {
      return ``
    }
  }

  return `
    <div class="result__info-box">
      <div class="result__info-title">
         

        <div class="result__info-options">
          ${renderUserConfig()}
        </div>
      </div>

      <div class="result__info-box-inner">

        <div class="result__row result__row--inner result__row--inner-header">
          <div class="row gx-0">
            <div class="col-5"> </div>
            <div class="col-3"> </div>
            <div class="col-2"> . </div>
            <div class="col-2"></div>
          </div>
        </div>

        ${renderCard()}

      </div><!--.result__info-box-inner-->

    </div><!--./result__info-box-->

    <div class="result__info-box">
      <div class="result__info-title"> </div>

      <div class="result__info-box-inner">

        <div class="result__row result__row--inner result__row--inner-header">
          <div class="row gx-0">
            <div class="col-9"> </div>
            <div class="col-3"></div>
          </div>
        </div>

        ${renderTraining()}

      </div><!--.result__info-box-inner-->

    </div><!--./result__info-box-->

    <div class="result__info-box">
      <div class="result__info-title">  </div>

      <div class="result__info-box-inner">

        <div class="result__row result__row--inner result__row--inner-header">
          <div class="row gx-0">
            <div class="col-9"> </div>
            <div class="col-3"></div>
          </div>
        </div>

        ${renderRecertification()}

      </div><!--.result__info-box-inner-->

    </div><!--./result__info-box-->
  `
}
