
/**
 *     
 *  @param {Object} user - 
 *  @param {number} [user.idUser] - id
 *  @param {string} [user.fio] - 
 *  @param {string} [user.division] - 
 *  @param {string} [user.department] - 
 *  @param {string} [user.work] -  
 *  @param {string} [user.status] -  
 *  @param {Object} options - 
 *  @param {number} [options.build] -      1-, 2- , 0-   Hse
 *  @return {string}
 * */
export function userInfoTemplate({idUser,fio,division,department,work,status},
                                 {build = 0} = {}
) {

  /**
   *  
   * @return {string}
   */
  const renderUserConfig = () => {

    //  
    if(build === 1){
      return  `
        <div class="col-4 g-justify-items-left js-matrix-work-hse" title="${work || ' '}">
          <span class="result__clip">
            ${work || ' '}
          </span>
        </div>
      `
    }
    //    BX
    else if (build === 2) {
      return  `
        <div class="col-4 g-justify-items-left js-matrix-work-hse" title="${work || ' '}">
          <span class="result__clip">
            ${work || ' '}
          </span>
        </div>
      `
    }
    //    BX  HSE
    else {
      return `
        <div class="col-4 js-matrix-work-hse">
          <span class="p-relative d-inline-block">
            <button class="button button--icon js-add-hse-modal" type="button" data-sumbiot-target="#add-hse-modal" data-id="${idUser}" title="  HSE">
              <img class="result__img" src="${BX.message('TemplateFolder')}/assets/img/add-document-icon.svg" width="18" height="18" alt="">
            </button>
          </span>
        </div>
      `
    }
  }

  return `
    <div class="col-1" title="ID: ${idUser}" data-id="${idUser}" data-sumbiot-page-counter></div>
    <div class="col-2" title=": ${department || ' '}">
      <span class="result__clip text-align-center">
        ${division || ' '}
      </span>
    </div>
    <div class="col-3 g-justify-items-left" title="${fio || ' '}">
      <span class="result__clip">
        ${fio || ' '}
      </span>
    </div>
    ${renderUserConfig()}
    <div class="col-1">${status || ' '}</div>
    <div class="col-1">
      <button class="result__options-arrow button button--arrow" type="button" title=""></button>
    </div>
  `
}
